export const createComponent = (comp, props) => {
  // functionæ˜¯å¯ä»¥ç›´æ¥è°ƒç”¨çš„ï¼Œä½†æ˜¯classæ˜¯éœ€è¦é€šè¿‡newå»åˆ›å»ºä¸€ä¸ªå®ä¾‹æ¥ä½¿ç”¨çš„
  // é€šè¿‡åˆ¤å®šåŸå‹æ˜¯å¦æœ‰renderæ–¹æ³•è¿›è¡Œåˆ¤åˆ«æ˜¯å¦ä¸ºclassç»„ä»¶
  // instanceofå°±æ˜¯é€šè¿‡åŸå‹é“¾æ¥è¿›è¡Œç±»å‹åˆ¤æ–­çš„,å› æ­¤æˆ‘ä»¬å¯ä»¥ç›´æ¥é‡‡ç”¨instanceofæ¥è¿›è¡Œåˆ¤æ–­
  // æˆ‘ä»¬èƒ½å¦åªæ£€æµ‹ React.Component çš„åä»£å‘¢ï¼Ÿ
  let inst;
  if (comp.prototype && comp.prototype.render) {
    console.log("ç±»ç»„ä»¶å—", comp);
    // å¦‚æœæ˜¯ç±»å®šä¹‰çš„ç»„ä»¶, åˆ™åˆ›å»ºæ–°çš„å®ä¾‹,è¿”å›
    inst = new comp(props);
  } else {
    // å¦‚æœæ˜¯å‡½æ•°ç»„ä»¶,å°†å‡½æ•°ç»„ä»¶æ‰©å±•ä¸ºç±»ç»„ä»¶ï¼Œæ–¹ä¾¿åé¢ç»Ÿä¸€ç®¡ç†
    inst = new Component(props);
    inst.constructor = comp;
    // å®šä¹‰renderå‡½æ•°
    inst.render = function () {
      return this.constructor(props);
    };
  }
  return inst;
};

export const renderComponent = (comp) => {
  let base;
  // è°ƒç”¨renderï¼Œè¿”å›jsxå¯¹è±¡
  const renderer = comp.render();
  // base = _render(renderer);
  base = diffNode(comp.base, renderer);
  if (comp.base && comp.componentWillUpdate) {
    comp.componentWillUpdate();
  }
  if (comp.base) {
    if (comp.componentDidUpdate) comp.componentDidUpdate();
  } else if (comp.componentDidMount) {
    comp.componentDidMount();
  }

  // èŠ‚ç‚¹æ›¿æ¢
  // if (comp.base && comp.base.parentNode) {
  //   comp.base.parentNode.replaceChild(base, comp.base);
  // }
  comp.base = base;
};

export const setComponentProps = (comp, props) => {
  if (!comp.base) {
    if (comp.componentWillMount) comp.componentWillMount();
  } else if (comp.componentWillReceiveProps) {
    comp.componentWillReceiveProps();
  }

  // è®¾ç½®ç»„ä»¶å±æ€§
  comp.props = props;
  // æ¸²æŸ“ç»„ä»¶
  renderComponent(comp);
};

const _render = (vnode) => {
  // renderæ–¹æ³•é‡Œé¢è§£ævnode
  if (
    typeof vnode === "undefined" ||
    vnode === null ||
    typeof vnode === "boolean"
  ) {
    return "";
  }

  if (typeof vnode === "number") {
    vnode = String(vnode);
  }

  // å¦‚æœvnodeæ˜¯å­—ç¬¦ä¸²
  if (typeof vnode === "string") {
    // åˆ›å»ºæ–‡æœ¬èŠ‚ç‚¹
    return document.createTextNode(vnode);
    // return container.appendChild(textNode);
  }

  // å¦‚æœtagæ˜¯å‡½æ•°,åˆ™æ¸²æŸ“ç»„ä»¶
  if (typeof vnode.tag === "function") {
    // 1. åˆ›å»ºç»„ä»¶
    const comp = createComponent(vnode.tag, vnode.attrs);
    // 2. è®¾ç½®ç»„ä»¶çš„å±æ€§
    // console.log(comp, "comp");
    setComponentProps(comp, vnode.attrs);
    // 3. ç»„ä»¶æ¸²æŸ“çš„èŠ‚ç‚¹å¯¹è±¡è¿”å›
    return comp.base;
  }

  // å¦åˆ™å°±æ˜¯ä¸€ä¸ªè™šæ‹ŸDOMå¯¹è±¡
  const { tag, attrs } = vnode;
  // åˆ›å»ºèŠ‚ç‚¹å¯¹è±¡
  let dom = document.createElement(tag);

  if (attrs) {
    // æœ‰å±æ€§
    Object.keys(attrs).forEach((key) => {
      const value = attrs[key];
      // console.log(value, key);
      // æ·»åŠ å±æ€§(domå…ƒç´ ï¼Œkey å±æ€§ valueå±æ€§å€¼)
      setAttibute(dom, key, value);
    });
  }
  // é€’å½’æ¸²æŸ“å­èŠ‚ç‚¹
  if (vnode.childrens) {
    vnode.childrens.forEach((child) => render(child, dom));
  }
  // return container.appendChild(dom);
  return dom;
};

/*
  æ·»åŠ å±æ€§
*/
export const setAttribute = (dom, key, value) => {
  // å°†å±æ€§åclassName è½¬æ¢ä¸ºclass
  if (key === "className") {
    key = "class";
  }
  // å¦‚æœæ˜¯äº‹ä»¶ onClick...
  if (/on\w+/.test(key)) {
    // è½¬ä¸ºå°å†™
    key = key.toLowerCase();
    dom[key] = value || "";
  } else if (key === "style") {
    if (!value || typeof value === "string") {
      dom.style.cssText = value || "";
    } else if (value && typeof value === "object") {
      // style={{ width: 20 }}
      for (let k in value) {
        if (typeof value[k] === "number") {
          dom.style[k] = `${value[k]}px`;
        } else {
          dom.style[k] = `${value[k]}`;
        }
      }
    }
  } else {
    // å…¶å®ƒå±æ€§
    if (key in dom) {
      dom[key] = value || "";
    }
    // valueæœ‰å€¼çš„è¯, æ›´æ–°å±æ€§
    if (value) {
      dom.setAttribute(key, value);
    } else {
      // æ— å€¼ç§»é™¤å±æ€§
      dom.removeAttribute(key);
    }
  }
};

