import { setAttribute, setComponentProps, createComponent } from "./index";
export const diff = (dom, vnode, container) => {
  // å¯¹æ¯”èŠ‚ç‚¹çš„å˜åŒ–
  const ret = diffNode(dom, vnode);
  if (container) {
    container.appendChild(ret);
  }
  return ret;
};

export const diffNode = (dom, vnode) => {
  let out = dom;
  if (vnode === undefined || vnode === null || typeof vnode === "boolean")
    vnode = "";
  if (typeof vnode === "number") vnode = String(vnode);
  // å¦‚æœvnodeæ˜¯å­—ç¬¦ä¸²
  if (typeof vnode === "string") {
    if (dom && dom.nodeType === 3) {
      if (dom.textContent !== vnode) {
        //æ›´æ–°æ–‡æœ¬å†…å®¹
        dom.textContent = vnode;
      }
    } else {
      out = document.createTextNode(vnode);
      if (dom && dom.parentNode) {
        dom.parentNode.replaceNode(out, dom);
      }
    }
    return out;
  }
  if (typeof vnode.tag === "function") {
    return diffComponent(out, vnode);
  }
  //  éæ–‡æœ¬DOMèŠ‚ç‚¹
  if (!dom) {
    out = document.createElement(vnode.tag);
  }
  // æ¯”è¾ƒå­èŠ‚ç‚¹(domèŠ‚ç‚¹å’Œç»„ä»¶)
  if (
    (vnode.childrens && vnode.childrens.length > 0) ||
    (out.childNodes && out.childNodes.length > 0)
  ) {
    // å¯¹æ¯”ç»„ä»¶ æˆ–è€…å­èŠ‚ç‚¹
    diffChildren(out, vnode.childrens);
  }
  diffAttribute(out, vnode);
  return out;
};
const diffComponent = (dom, vnode) => {
  let comp = dom;
  // å¦‚æœç»„ä»¶æ²¡æœ‰å˜åŒ–,é‡æ–°è®¾ç½®props
  if (comp && comp.constructor === vnode.tag) {
    // é‡æ–°è®¾ç½®props
    setComponentProps(comp, vnode.attrs);
    // èµ‹å€¼
    dom = comp.base;
  } else {
    // ç»„ä»¶ç±»å‹å‘ç”Ÿå˜åŒ–
    if (comp) {
      // å…ˆç§»é™¤æ—§çš„ç»„ä»¶
      unmountComonent(comp);
      comp = null;
    }
    // 1.åˆ›å»ºæ–°ç»„ä»¶
    comp = createComponent(vnode.tag, vnode.attrs);
    // 2.è®¾ç½®ç»„ä»¶å±æ€§
    setComponentProps(comp, vnode.attrs);
    // 3.ç»™å½“å‰æŒ‚è½½base
    dom = comp.base;
  }
  return dom;
};
const unmountComonent = (comp) => {
  removeNode(comp.base);
};
const removeNode = (dom) => {
  if (dom && dom.parentNode) {
    dom.parentNode.removeNode(dom);
  }
};
const diffChildren = (dom, vchildren) => {
  const domChildren = dom.childNodes;
  const children = [];
  const keyed = {};

  // å°†æœ‰keyçš„èŠ‚ç‚¹(ç”¨å¯¹è±¡ä¿å­˜)å’Œæ²¡æœ‰keyçš„èŠ‚ç‚¹(ç”¨æ•°ç»„ä¿å­˜)åˆ†å¼€
  if (domChildren.length > 0) {
    [...domChildren].forEach((item) => {
      // è·å–key
      const key = item.key;
      if (key) {
        // å¦‚æœkeyå­˜åœ¨,ä¿å­˜åˆ°å¯¹è±¡ä¸­
        keyed[key] = item;
      } else {
        // å¦‚æœkeyä¸å­˜åœ¨,ä¿å­˜åˆ°æ•°ç»„ä¸­
        children.push(item);
      }
    });
  }
  if (vchildren && vchildren.length > 0) {
    let min = 0;
    let childrenLen = children.length;
    [...vchildren].forEach((vchild, i) => {
      // è·å–è™šæ‹ŸDOMä¸­æ‰€æœ‰çš„key
      const key = vchild.key;
      let child;
      if (key) {
        // å¦‚æœæœ‰key,æ‰¾åˆ°å¯¹åº”keyå€¼çš„èŠ‚ç‚¹
        if (keyed[key]) {
          child = keyed[key];
          keyed[key] = undefined;
        }
      } else if (childrenLen > min) {
        // å¦‚æœæ²¡æœ‰key,åˆ™ä¼˜å…ˆæ‰¾ç±»å‹ç›¸åŒçš„èŠ‚ç‚¹
        for (let j = min; j < childrenLen; j++) {
          let c = children[j];
          if (c) {
            child = c;
            children[j] = undefined;
            if (j === childrenLen - 1) childrenLen--;
            if (j === min) min++;
            break;
          }
        }
      }
      // å¯¹æ¯”
      child = diffNode(child, vchild);
      // æ›´æ–°DOM
      const f = domChildren[i];

      if (child && child !== dom && child !== f) {
        // å¦‚æœæ›´æ–°å‰çš„å¯¹åº”ä½ç½®ä¸ºç©ºï¼Œè¯´æ˜æ­¤èŠ‚ç‚¹æ˜¯æ–°å¢çš„
        if (!f) {
          dom.appendChild(child);
          // å¦‚æœæ›´æ–°åçš„èŠ‚ç‚¹å’Œæ›´æ–°å‰å¯¹åº”ä½ç½®çš„ä¸‹ä¸€ä¸ªèŠ‚ç‚¹ä¸€æ ·ï¼Œè¯´æ˜å½“å‰ä½ç½®çš„èŠ‚ç‚¹è¢«ç§»é™¤äº†
        } else if (child === f.nextSibling) {
          removeNode(f);
          // å°†æ›´æ–°åçš„èŠ‚ç‚¹ç§»åŠ¨åˆ°æ­£ç¡®çš„ä½ç½®
        } else {
          // æ³¨æ„insertBeforeçš„ç”¨æ³•ï¼Œç¬¬ä¸€ä¸ªå‚æ•°æ˜¯è¦æ’å…¥çš„èŠ‚ç‚¹ï¼Œç¬¬äºŒä¸ªå‚æ•°æ˜¯å·²å­˜åœ¨çš„èŠ‚ç‚¹
          dom.insertBefore(child, f);
        }
      }
    });
  }
};
const diffAttribute = (dom, vnode) => {
  // ä¿å­˜ä¹‹å‰çš„DOMçš„æ‰€æœ‰å±æ€§
  const oldAttrs = {};
  const newAttrs = vnode.attrs;
  //dom æ˜¯åŸæœ‰çš„èŠ‚ç‚¹å¯¹è±¡  vnode è™šæ‹ŸDOM
  const domAttrs = dom.attributes;
  [...domAttrs].forEach((item) => {
    // console.log(item.name,item.value);
    oldAttrs[item.name] = item.value;
  });

  // æ¯”è¾ƒ
  // å¦‚æœåŸæ¥å±æ€§è·Ÿæ–°çš„å±æ€§å¯¹æ¯”,ä¸åœ¨æ–°çš„å±æ€§ä¸­,åˆ™å°†å…¶ç§»é™¤æ‰ (å±æ€§å€¼ä¸ºundefined)
  for (let key in oldAttrs) {
    if (!(key in newAttrs)) {
      setAttribute(dom, key, undefined);
    }
  }
  // æ›´æ–° class='active'  abc

  for (let key in newAttrs) {
    if (oldAttrs[key] !== newAttrs[key]) {
      // å€¼ä¸åŒ,æ›´æ–°å€¼
      setAttribute(dom, key, newAttrs[key]);
    }
  }
};
